import React from 'react';

export default class Index extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            name: "",
        }
    }

    render() {
        return (
            <div className={"input-div"}>
                <input type="text"
                       value={this.state.name}
                       placeholder="资源名称~~"
                       onChange={val => {
                           this.setState({name: val.target.value})
                       }}
                       onKeyDown={event => {
                           if (event.keyCode === 13) {
                               this.doSearch();
                           }
                       }}
                />
                <svg onClick={() => {
                    this.doSearch();
                }} t="1577173041895" className="icon" viewBox="0 0 1024 1024" version="1.1"
                     xmlns="http://www.w3.org/2000/svg" p-id="2624" width="200" height="200">
                    <path
                        d="M977.454545 1024c-11.636364 0-23.272727-4.654545-32.581818-13.963636l-232.727272-232.727273c-18.618182-18.618182-18.618182-46.545455 0-65.163636s46.545455-18.618182 65.163636 0l232.727273 232.727272c18.618182 18.618182 18.618182 46.545455 0 65.163637-9.309091 9.309091-20.945455 13.963636-32.581819 13.963636z"
                        fill="#176AFD" p-id="2625"></path>
                    <path
                        d="M442.181818 93.090909c193.163636 0 349.090909 155.927273 349.090909 349.090909s-155.927273 349.090909-349.090909 349.090909S93.090909 635.345455 93.090909 442.181818 249.018182 93.090909 442.181818 93.090909m0-93.090909C197.818182 0 0 197.818182 0 442.181818s197.818182 442.181818 442.181818 442.181818 442.181818-197.818182 442.181818-442.181818S686.545455 0 442.181818 0z"
                        fill="#1D2633" p-id="2626"></path>
                </svg>
            </div>
        );
    }

    doSearch = () => {
        this.props.history.push("/list?name=" + this.state.name)
    }

}