import React from 'react';
import querystring from "querystring";
import {getDetail} from "../api/movies";

export default class Play extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
        }
    }

    componentDidMount() {
        let search = this.props.location.search.replace("?", "");
        let params = querystring.parse(search);
        getDetail(params).then(data => {
            this.setState({ data: data.data})
        })
    }

    render() {
        return (
            <div className="detail">
                <div className="detail-list">{this.state.data.map(item => <span key={item.href}
                    onClick={() => {
                        this.doPlay(item.href)
                    }}
                    className="detail-item">{item.name}</span>)}
                </div>
            </div>
        );
    }

    doPlay = (url) => {
        this.props.history.push("/playing?url=" + url)
    }
}