import request from "../util/request";

export const getMovies= params => {
    let options = {
        method: 'GET',
        url: '/movives/resource',
        params
    };
    return request(options);
}


export const getDetail= params => {
    let options = {
        method: 'GET',
        url: '/movives/detail',
        params
    };
    return request(options);
}