import React from 'react';
import Player from 'griffith'
import querystring from "querystring";
import ReactHLS from 'react-hls';
import {PcOrMobile} from "../util/request";

export default class Playing extends React.Component {

    render() {
        let search = this.props.location.search.replace("?", "");
        let params = querystring.parse(search);
        if (!PcOrMobile()) {
            return (
                <ReactHLS autoplay width="100%" height="100%" url={params.url}/>
            )
        } else {
            const source = {
                hd: {
                    format: 'm3u8',
                    play_url: params.url,
                },
            };
            return (
                <Player sources={source} autoplay/>
            );
        }
    }
}