import React from 'react';
import {getMovies} from "../api/movies"
import querystring from "querystring";
import {CopyToClipboard} from 'react-copy-to-clipboard';

export default class List extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
        }
    }

    componentDidMount() {
        let search = this.props.location.search.replace("?", "");
        var params = querystring.parse(search);
        this.setState({
            name: params.name
        });
        getMovies(params).then(data => {
            this.setState({data: data.data})
        })
    }

    render() {
        return (
            <div className="list">
                {this.state.data.map(item => <div className="item">
                        <span className="item-name">{item.title}</span>
                        <span>
                        <CopyToClipboard text={item.href}>
                           <svg t="1577176210350" className="icon" viewBox="0 0 1024 1024" version="1.1"
                                xmlns="http://www.w3.org/2000/svg" p-id="4290" width="200" height="200"><path
                               d="M556.544 606.08a138.176 138.176 0 0 1-98.048-40.544 18.88 18.88 0 0 1 0-26.624 18.88 18.88 0 0 1 26.624 0 101.056 101.056 0 0 0 142.816 0l136.928-136.928a101.056 101.056 0 0 0 0-142.816 101.056 101.056 0 0 0-142.816 0l-115.52 115.52a18.88 18.88 0 0 1-26.624 0 18.88 18.88 0 0 1 0-26.624l115.52-115.52a138.752 138.752 0 0 1 196.064 0 138.784 138.784 0 0 1 0 196.064l-136.928 136.896a138.144 138.144 0 0 1-98.08 40.576h0.064z"
                               p-id="4291" fill="#d81e06"></path><path
                               d="M330.592 832a138.176 138.176 0 0 1-98.048-40.544 138.752 138.752 0 0 1 0-196.064l136.896-136.896a138.752 138.752 0 0 1 196.064 0 18.88 18.88 0 0 1 0 26.624 18.88 18.88 0 0 1-26.624 0 101.056 101.056 0 0 0-142.816 0l-136.896 136.928a101.056 101.056 0 0 0 0 142.816 101.056 101.056 0 0 0 142.816 0l115.52-115.52a18.88 18.88 0 0 1 26.624 0 18.88 18.88 0 0 1 0 26.624l-115.52 115.52A138.24 138.24 0 0 1 330.56 832h0.032z"
                               p-id="4292" fill="#d81e06"></path>
                           </svg>
                        </CopyToClipboard>
                       <svg onClick={() => {
                           this.doPlay(item.href);
                       }} t="1577176725389" className="icon" viewBox="0 0 1024 1024" version="1.1"
                            xmlns="http://www.w3.org/2000/svg" p-id="5142" width="200" height="200"><path
                           d="M803.84 492.032 213.056 135.04C195.392 124.288 174.208 124.288 156.352 134.976 138.624 145.728 128 164.928 128 186.368l0 714.176c0 21.44 10.624 40.704 28.352 51.392C165.248 957.312 174.976 960 184.704 960c9.728 0 19.52-2.688 28.352-8.064l590.72-356.992c17.728-10.752 28.288-30.016 28.288-51.456C832.128 521.984 821.568 502.72 803.84 492.032zM189.568 891.712 187.136 193.792l578.624 349.696L189.568 891.712z"
                           p-id="5143" fill="#13227a"></path>
                       </svg>
                    </span>
                    </div>
                )}
            </div>
        );
    }

    doPlay = (url) => {
        this.props.history.push("/play?url=" + url)
    }

}